.card {
  position: relative;
  width: 160px;
  height: 220px;
  perspective: 800px;
  transform-style: preserve-3d;
  transition: transform .5s, opacity .2s;
  backface-visibility: hidden;
  cursor: pointer; }

.card--open {
  transform: rotateY(180deg); }

.card--guessed {
  opacity: 0; }

.card img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden; }

.front {
  transform: rotateY(180deg); }



